/* top-left */

.custom-top-container {
    .top-left {
        top: 0;
        left: 0;

        @media #{$small_mobile} {
            text-align: center;

            .logo {
                width: 80vw;
            }
        }

        .logo {
            width: 25vh;
        }
    }

    .top-right {
        top: 0;
        right: 0;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        padding: 1vh;

        span {
            &:nth-child(1) {
                font-size: 2.6vh;
                color: #000;
            }

            &:nth-child(2) {
                font-size: 1.0416vw;
                border-radius: 0.5vw;
                padding: 0.5vw;
                margin: 0vw 0.5vw;
            }
        }

        a {
            padding: 0vw 0vw;
            font-size: 1vw;
            border-radius: 50%;
            cursor: pointer;
            background: #fff;
            cursor: pointer;

            img {
                width: 10vh;
            }
        }
    }

    .top-center {
        top: 30vh;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);

        .navbar-collapse {
            @media #{$small_mobile} {
                position: relative;
                top: unset;
                right: unset;
                background-color: #FFF;
            }
        }

        .navbar-header {
            .navbar-brand.visible-xs {
                color: #000;
            }
        }

        .navbar-nav {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-pack: distribute;
            justify-content: space-around;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;

            @media #{$small_mobile} {
                width: 100%;
            }

            li {
                padding: 0;

                a {
                    margin: 5vh 2vh 0vh 2vh;
                    padding: 1.5vh 1vh 1.5vh 1.65vh;
                    font-size: 2vh;
                    min-width: max-content;
                    font-weight: bold;

                    @media #{$tab_device} {
                        margin: 5vh 0.5vh 0vh 0.5vh;
                        padding: 1.5vh 1vh 1.5vh 1.65vh;
                    }

                    @media #{$from_0_to_767} {
                        margin: 1vh 0vh 1vh 0vh;
                    }

                    @media #{$small_mobile} {
                        font-size: 2vh;
                        width: 100%;
                        text-align: left;
                        padding: 0.2vw 2.65vw;
                        border-bottom: 1px solid #dedede;
                    }


                }
            }

            .dropdown-menu {
                li {
                    a {
                        margin-top: 0vh;
                    }
                }
            }
        }

        .trans {
            display: flex;
            -ms-flex-pack: distribute;
            justify-content: space-around;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            background: rgba(255, 255, 255, 0.75);
            border-radius: 1.95vw;
            margin: 0vw auto;
            padding: 1vw;
            width: 28vw;

            p {
                font-size: 3vh;
            }

            a {
                padding: 0vw 0vw;
                font-size: 1vw;
                border-radius: 50%;
                cursor: pointer;
                background: #fff;
                cursor: pointer;

                img {
                    width: 15vh;

                    @media #{$small_mobile} {
                        width: 25vw;
                    }
                }
            }
        }
    }

    .bottom-center {
        bottom: -33vh;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        background-color: #FFF;
        border: 3px solid #0b661d;
        border-radius: 1.30vw;
        z-index: 888;
        min-width: 70vw;
        padding: 3vh 2.5vh 1vh !important;
        -webkit-box-shadow: 2px 7px 5px 0px rgba(0, 0, 0, 0.9);
        box-shadow: 2px 7px 5px 0px rgba(0, 0, 0, 0.9);

        @media #{$tab_device} {
            bottom: -15vh;

            // from 768 to 991
            ul {
                li {
                    a {
                        min-width: 12vh;
                        padding: 1.75vh;
                        font-size: 1.5vh;
                    }
                }
            }
        }

        p {
            font-size: 3vh;
        }

        ul {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: justify;
            -ms-flex-pack: justify;
            justify-content: space-between;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            padding: 0px;

            li {
                -webkit-box-flex: 1;
                -ms-flex-positive: 1;
                flex-grow: 1;

                a {
                    border: 1px solid #000;
                    border-radius: 1.5vh;
                    text-decoration: none;
                    margin: 1vh;
                    width: 90%;
                    display: block;
                    text-align: center;
                    min-width: 9vw;
                    padding: 3.5vh;
                    font-size: 2.25vh;

                    @media #{$tab_device} {
                        padding: 1.75vh;
                        font-size: 1.5vh;
                    }

                    &:hover {
                        background-color: #000;
                        color: #FFF;
                    }

                }
            }
        }
    }
}

