.leasing-calc-top {
    min-height: 42.32vw;
    background-color: #FFF;

    @media #{$small_mobile} {
        margin-top: 2rem;
    }

    .L-container {
        margin-top: 0rem;
        background-color: #cccccc4d;

        h2 {
            padding: 0.5vh;
            font-size: 3.5vh;
        }

        .L-content {
            position: relative;
            background-color: #FFF;
            min-height: 60%;
            margin: 3rem 0rem 0px 10rem;
            border: 1px solid #0003;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            padding: 2rem 20rem 0rem 5rem;
            -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
            box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);

            .item {
                margin-bottom: 3.5rem;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-wrap: wrap;
                flex-wrap: wrap;

                #cash-error {
                    display: none;
                    position: absolute;
                    z-index: 9999;
                    top: 0;
                    background: #fddfdf;
                    color: red !important;
                    padding: 5px;
                    border-radius: 4%;
                    -webkit-box-shadow: 4px 4px 19px #948e8e;
                    box-shadow: 4px 4px 19px #948e8e;
                    border: 2px dotted #948e8e;
                    left: 50vw;
                    -webkit-transform: translate(-25vw, 0vw);
                    transform: translate(-25vw, 0vw);
                }

                .err-label {
                    display: block !important;
                }

                .input-title {
                    font-weight: bold;
                    font-size: 16px;
                    -webkit-box-flex: 47%;
                    -ms-flex: 47%;
                    flex: 47%;
                }

                span {
                    float: left;
                }

                input,
                select {
                    float: right;
                    height: 4rem;
                    font-size: 16px;
                    font-weight: bold;
                    -webkit-box-flex: 47%;
                    -ms-flex: 47%;
                    flex: 47%;
                }

                .seekbar {
                    -webkit-box-flex: 100%;
                    -ms-flex: 100%;
                    flex: 100%;
                }

                p {
                    color: #676767;
                    margin-top: 2rem;
                    text-align: center;
                }

                .shapes {
                    display: -webkit-box;
                    display: -ms-flexbox;
                    display: flex;
                    -webkit-box-pack: justify;
                    -ms-flex-pack: justify;
                    justify-content: space-between;
                    -webkit-box-flex: 90%;
                    -ms-flex: 90%;
                    flex: 90%;
                    flex-wrap: wrap;

                    input[type="radio"] {
                        display: none;

                        &:checked+label {
                            background-color: #038c02 !important;
                            color: #FFF;
                            background: #038c02 !important;
                        }
                    }

                    .shape {
                        position: relative;
                        cursor: pointer;
                        min-height: 20vh;
                        -webkit-clip-path: polygon(0% 0%, 100% 0, 100% 65%, 51% 100%, 0 65%);
                        clip-path: polygon(0% 0%, 100% 0, 100% 65%, 51% 100%, 0 65%);
                        color: #000;
                        background: white;
                        background: -webkit-gradient(left top, left bottom, color-stop(0%, white), color-stop(47%, #f6f6f6), color-stop(100%, #dddddd));
                        background: -webkit-gradient(linear, left top, left bottom, from(white), color-stop(47%, #f6f6f6), to(#dddddd));
                        background: linear-gradient(to bottom, white 0%, #f6f6f6 47%, #dddddd 100%);
                        border: 1px solid #777;
                        -ms-flex-preferred-size: 15rem;
                        flex-basis: 15rem;
                        display: -webkit-box;
                        display: -ms-flexbox;
                        display: flex;
                        -webkit-box-orient: vertical;
                        -webkit-box-direction: normal;
                        -ms-flex-direction: column;
                        flex-direction: column;
                        -webkit-box-align: center;
                        -ms-flex-align: center;
                        align-items: center;
                        margin: 1vh;

                        &:hover {
                            opacity: 0.7;
                        }

                        span {
                            &:nth-child(1) {
                                font-weight: bold;
                                font-size: 2vh;
                                width: 100%;
                                text-align: center;
                            }

                            &:nth-child(2) {
                                position: absolute;
                                font-size: 2vh;
                                bottom: 5vh;
                            }
                        }
                    }
                }
            }
        }

        .boxes-container {
            position: absolute;
            right: -12rem;
            top: 50%;
            -webkit-transform: translate(5%, -50%);
            transform: translate(5%, -50%);

