/* range input*/

.seekbar {
    margin: 12px 0;
    padding: 10px 24px;
    position: relative;
}

.seekbar-progress {
    height: 24px;
    background-color: #f5f5f5;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#ebebeb), to(#f5f5f5));
    background-image: linear-gradient(to bottom, #ebebeb 0%, #f5f5f5 100%);
    background-repeat: repeat-x;
    border-radius: 6px;

    [role="progressbar"] {
        height: 100%;
        position: relative;
        background: #0b661d;
        border-radius: 6px;
        -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
        box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);

        &:after {
            content: "+";
            display: block;
            width: 24px;
            height: 24px;
            position: absolute;
            top: 0px;
            right: -12px;
            background-color: #000;
            color: #FFF;
            border: 1px solid #999;
            border-radius: 6px;
            font-size: 22px;
            line-height: 1;
            text-align: center;
        }
    }
}

.seekbar input[type="range"] {
    -webkit-appearance: none;
    width: 100%;
    height: 100%;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    background: transparent;
    outline: 0;
    border: 0;

    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        display: block;
        width: 48px;
        height: 48px;
        background-color: transparent;
    }

    &::-moz-range-thumb {
        display: block;
        width: 48px;
        height: 48px;
        background: transparent;
        border: 0;
    }

    &::-moz-range-track {
        background: transparent;
        border: 0;
    }

    &::-moz-focus-outer {
        border: 0;
    }
}

